import React from 'react'
import Header from '../../components/Home/HeaderSection/Header'
import Banner from './Components/Banner/Banner'
import EmployeeHealthcare from './Components/Employee-Healthcare/EmployeeHealthcare'
import InsuranceDetail from './Components/Insurance/InsuranceDetail'
import BecomeMember from './Components/OnsurityMembtn/BecomeMember'
import Partners from './Components/OurPartners/Partners'
import PlanType from './Components/PlanList/PlanType';
import FooterComponent from '../../components/Common/Footer/footer'
import Feature from './Components/Feature-Section/Feature'
import ComparisonFaq from './Components/Comparison-Faq/ComparisonFaq'

export default function HomePage() {
    return (
        <div>
            <Header />
            <Banner />
            <EmployeeHealthcare />
            <InsuranceDetail />
            <PlanType />
            <BecomeMember title="Start your comprehensive employee healthcare membership at Just Rs. 145 per month" value="Become an Onsurity Member" />
            <Feature />
            <BecomeMember title="Expand your employees’ benefits beyond insurance at Rs. 145/month" value="Get a Quote" />
            <Partners />
            <ComparisonFaq />
            <div style={{width: '85%',margin: 'auto'}}>
               <FooterComponent />
            </div>
        </div>
    )
}