import React from 'react';
import Button from '../../../../Input-controls/Button/Button';
import './BecomeMember.scss';

function BecomeMember(props) {
    return (
        <div className="bcm-btncls">
            <div className="btn-flexx">
                <div className="btn-cntnt">{props.title}</div>
                <div style={{marginTop: '50px',textAlign: 'center'}}>
                    <Button className="onu-mem-btn" value={props.value} />
                </div>
            </div>
        </div>
    );
}

export default BecomeMember;