import React, { Suspense, useState } from 'react'
import FooterComponent from '../../components/Common/Footer/footer';
import { urls } from '../../routes/routes';
import Banner from './Components/Banner';
import CustomerReview from './Components/CustomerReview';
import FounderReview from './Components/FounderReview';
import GetInTouch from './Components/GetInTouch';
import TestimonialDetail from './Components/TestimonialDetail';
import Videosec from './Components/Videosec';
import './Testimonial.scss'
const Header = React.lazy(() => import('../../components/Home/HeaderSection/Header'));
const Slides = React.lazy(() => import('../../components/Common/Sliders/Slide5/Slide'));

const slide1 = [
    {
        "head": "",
        "sub": "",
        "currentCls": "",
        "imgPath": "https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594275941/New-home-screen/HomeSlide_1/slide1_1_djvzzu.svg"
    }
]
const contentData = {
    "Head": "",
    "subcontent": ""
};

function Testimonial(props) {

    const[callUrl, setcallUrl] = useState(true)

    return (
        <div>
            <div className="HeaderParentCls1">
                <Suspense fallback={<div></div>}>
                    <Header />
                </Suspense>
            </div>

            <Banner />
            <TestimonialDetail />
            <GetInTouch  
                title="Make Your Employee Healthcare Experience Comprehensive" 
                btnText="Get a Quote"
                callUrl={callUrl}
                link={urls.VERIFY_PHONE}
                 />
            <CustomerReview />
            <Videosec />
            <GetInTouch 
                title="Thinking of Onsurity for your team?" 
                btnText="Get in touch"
                link={urls.PRODUCT_PAGE_NEW}
                 />
            <FounderReview />
            <div style={{marginTop: '80px'}}>
               <GetInTouch 
                    title="It's your turn to lead the way with your employee healthcare initiative " 
                    btnText="Get a Quote"
                    link={urls.PRODUCT_PAGE_NEW}
                     />
            </div>
           
            <div className="footer-compo">
                <FooterComponent />
            </div>

        </div>
    )
}

export default Testimonial;
